<template>
<div>
  <v-card flat v-for="(subsection, subsectionIndex) in section.subsections" :key=subsectionIndex :to="section.id+'/'+subsection.id">
      <v-card-title>{{subsection.title}}</v-card-title>
  </v-card>
</div>
</template>

<script>
  export default {
    computed: {
      section: function(){
        var component = this;
        return this.$store.state.profile.sections.find(function(item){
          return item.id == component.$route.params.sectionID;
        });
      }
    }
  }
</script>